import React, { useEffect, useState } from 'react'
import './footer.scss'

import logo from '../../assets/logo/Netflix.png'


import { Link } from 'react-router-dom'




const Footer = () => {
  let isEnglishs = localStorage.getItem('language') ? false : true

  return (
    <div className='footer'>
      <div className="footer__content container">
        <div className="footer__content__logo">
          <div className="logo">
            <img src={logo} alt="" />
          </div>
        </div>
        {
          isEnglishs ?
            <>
              <div className="questions">
                <Link to="/">Questions? Contact us.</Link>
              </div>
              <div className="footer__content__menus">
                <div className="footer__content__menu">
                  <Link to="/">FAQ</Link>
                  <Link to="/">Investor Relations</Link>
                  <Link to="/">Privacy</Link>
                  <Link to="/">Speed Test</Link>
                </div>
                <div className="footer__content__menu">
                  <Link to="/">Help Center</Link>
                  <Link to="/">Jobs</Link>
                  <Link to="/">Cookie Preferences</Link>
                  <Link to="/">Legal Notices</Link>
                </div>
                <div className="footer__content__menu">
                  <Link to="/">Account</Link>
                  <Link to="/">Ways to Watch</Link>
                  <Link to="/">Corporate Information</Link>
                  <Link to="/">Only on Netflix</Link>
                </div>
                <div className="footer__content__menu">
                  <Link to="/">Media Center</Link>
                  <Link to="/">Terms of Use</Link>
                  <Link to="/">Contact Us</Link>
                </div>
              </div>
            </>
            :
            <>
              <div className="questions">
                <Link to="/">Cu hi? Lin h chng ti.</Link>
              </div>
              <div className="footer__content__menus">
                <div className="footer__content__menu">
                  <Link to="/">FAQ</Link>
                  <Link to="/">Nh u t</Link>
                  <Link to="/">Chnh sch ring t</Link>
                  <Link to="/">Tc </Link>
                </div>
                <div className="footer__content__menu">
                  <Link to="/">Trung tm tr gip</Link>
                  <Link to="/">Cng vic</Link>
                  <Link to="/">Ty chn cookie</Link>
                  <Link to="/">Thng bo php l</Link>
                </div>
                <div className="footer__content__menu">
                  <Link to="/">Ti khon</Link>
                  <Link to="/">Cch xem</Link>
                  <Link to="/">Thng tin cng ty</Link>
                  <Link to="/">Ch trn Netflix</Link>
                </div>
                <div className="footer__content__menu">
                  <Link to="/">Trung tm Truyn thng</Link>
                  <Link to="/">Terms of Use</Link>
                  <Link to="/">Lin h chng ti</Link>
                </div>
              </div>
            </>
        }
      </div>

    </div>
  )
}

export default Footer